import React, { useState } from 'react';
import './App.css';
import Resources from "./Components/Ressources";
import Machinery from "./Components/Machinery";

function App() {

  const[lemons, setLemons] = useState(0);
  const[juice, setJuice] = useState(0);
  const[lemonade, setLemonade] = useState(0);
  const[money, setMoney] = useState(0);
  const[electricity, setElectricity] = useState(0);
  const[science, setScience] = useState(0);

  const[lemontree, setLemontree] = useState(80);
  const[squeezer, setSqueezer] = useState(40);
  const[mixer, setMixer] = useState(10);
  const[booth, setBooth] = useState(10);
  const[acid, setAcid] = useState(10);
  const[research, setResearch] = useState(10);

  return (
    <>
    <Resources lemons={lemons} setLemons={setLemons} juice={juice} setJuice={setJuice} lemonade={lemonade} setLemonade={setLemonade} money={money} setMoney={setMoney} electricity={electricity} setElectricity={setElectricity} science={science} setScience={setScience}/>
    <Machinery money={money} setMoney={setMoney} research={research} setResearch={setResearch} acid={acid} setAcid={setAcid} booth={booth} setBooth={setBooth} mixer={mixer} setMixer={setMixer} squeezer={squeezer} setSqueezer={setSqueezer} lemontree={lemontree} setLemontree={setLemontree} lemons={lemons} setLemons={setLemons} juice={juice} setJuice={setJuice} lemonade={lemonade} setLemonade={setLemonade} electricity={electricity} setElectricity={setElectricity} science={science} setScience={setScience}/> 
    </>
  );
}

export default App;
