import React, { useEffect, useState } from 'react';
import "../css/components.css";

function Machinery(props) {
    
    const [counter, setCounter] = useState(0);
    const timer = () => setCounter(counter - 1);

    function buyLemontree() {
        if (props.money >= 20) {
            props.setMoney(props.money-20);
            props.setLemontree(props.lemontree+1);
        }
    }

    function buySqueezer() {
        if (props.money >= 100) {
            props.setMoney(props.money-100);
            props.setSqueezer(props.squeezer+1);
        }
    }

    function buyMixer() {
        if (props.money >= 250) {
            props.setMoney(props.money-250);
            props.setMixer(props.mixer+1);
        }
    }

    function buyBooth() {
        if (props.money >= 500) {
            props.setMoney(props.money-500);
            props.setBooth(props.booth+1);
        }
    }

    function buyAcid() {
        if (props.money >= 1200) {
            props.setMoney(props.money-1200);
            props.setAcid(props.acid+1);
        }
    }

    function buyResearch() {
        if (props.money >= 2600) {
            props.setMoney(props.money-2600);
            props.setResearch(props.research+1);
        }
    }

    function tickeverySecond() {
        var tlemon = props.lemons;
        var tjuice = props.juice;
        var tlemonade = props.lemonade;
        var tmoney = props.money;
        var telectricity = props.electricity;
        var tscience = props.science;
        if (props.research >= 1) 
            if (telectricity >= (5*props.research)) {
                telectricity-=5*props.research;
                tscience += 1*props.research;
        }
        if (props.acid >= 1) 
            if (tjuice >= (12*props.acid)) {
                tjuice-=12*props.acid;
                telectricity += 1*props.acid;
        }
        if (props.booth >= 1) 
            if (tlemonade >= (1*props.booth)) {
                tlemonade-=1*props.booth;
                tmoney += 2*props.booth;
        }
        if (props.mixer >= 1) 
            if (tjuice >= (4*props.mixer)) {
                tjuice-=4*props.mixer;
                tlemonade += 1*props.mixer;
        }
        if (props.squeezer >= 1) 
            if (tlemon >= (2*props.squeezer)) {
                tlemon-=2*props.squeezer;
                tjuice+=1*props.squeezer;
        }
        if (props.lemontree >= 1) 
            tlemon += 1*props.lemontree;
        
        props.setLemons(tlemon);
        props.setJuice(tjuice);
        props.setLemonade(tlemonade);
        props.setMoney(tmoney);
        props.setElectricity(telectricity);
        props.setScience(tscience);
    }

    useEffect(() => {
        if (counter <= 0) {
            tickeverySecond();
        }
        const id = setInterval(timer, 1000);
        return () => clearInterval(id);
      }, [counter]);

return (
    <div id="middlebar" className="machinery">
        <h2>Machinery:</h2>
        <div id="machinery" className="table">
            <table>
                <thead>
                    <tr>
                        <th className="legend"></th>
                        <th className="clickable clickable lemontree" onClick={buyLemontree}>Lemontree</th>
                        <th className="clickable squeezer" onClick={buySqueezer}>Squeezer</th>
                        <th className="clickable mixer" onClick={buyMixer}>Mixer</th>
                        <th className="clickable booth" onClick={buyBooth}>Sales booth</th>
                        <th className="clickable acidgen" onClick={buyAcid}>Acid Generator</th>
                        <th className="clickable research" onClick={buyResearch}>Research Center</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td className="legend">Amout:</td>
                        <td className="clickable lemontree" onClick={buyLemontree}>{props.lemontree}</td>
                        <td className="clickable squeezer" onClick={buySqueezer}>{props.squeezer}</td>
                        <td className="clickable mixer" onClick={buyMixer}>{props.mixer}</td>
                        <td className="clickable booth" onClick={buyBooth}>{props.booth}</td>
                        <td className="clickable acidgen" onClick={buyAcid}>{props.acid}</td>
                        <td className="clickable research" onClick={buyResearch}>{props.research}</td>
                    </tr>
                    <tr>
                        <td className="legend">Costs:</td>
                        <td className="clickable lemontree" onClick={buyLemontree}>20 Dollar</td>
                        <td className="clickable squeezer" onClick={buySqueezer}>100 Dollar</td>
                        <td className="clickable mixer" onClick={buyMixer}>250 Dollar</td>
                        <td className="clickable booth" onClick={buyBooth}>500 Dollar</td>
                        <td className="clickable acidgen" onClick={buyAcid}>1200 Dollar</td>
                        <td className="clickable research" onClick={buyResearch}>2600 Dollar</td>
                    </tr>
                    <tr>
                        <td className="legend">Services per Second:</td>
                        <td className="clickable lemontree" onClick={buyLemontree}>One Free Lemon</td>
                        <td className="clickable squeezer" onClick={buySqueezer}>Two Lemons for One Juice</td>
                        <td className="clickable mixer" onClick={buyMixer}>Four Juice for One Lemonade</td>
                        <td className="clickable booth" onClick={buyBooth}>One Lemonade for Two Dollar</td>
                        <td className="clickable acidgen" onClick={buyAcid}>Twelve Juice for 1 Electricity</td>
                        <td className="clickable research" onClick={buyResearch}>5 Electricity for 1 Science</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
  );
}

export default Machinery;