import React from 'react';
import "../css/components.css";


function Resources(props) {

    function format(input){
        var suffix = [
            '', 'K', 'M', 'B', 'T', 'Qa', 'Qi', 'Sx', 'Sp', 'Oc', 'No', 'Dc', 'Ud',
            'Dd', 'Td', 'Qad', 'Qid', 'Sxd', 'Spd', 'Od', 'Nd', 'V', 'Uv', 'Dv',
            'Tv', 'Qav', 'Qiv', 'Sxv', 'Spv', 'Ov', 'Nv', 'Tg', 'Utg', 'Dtg', 'Ttg',
            'Qatg', 'Qitg', 'Sxtg', 'Sptg', 'Otg', 'Ntg', 'Qaa', 'Uqa', 'Dqa', 'Tqa',
            'Qaqa', 'Qiqa', 'Sxqa', 'Spqa', 'Oqa', 'Nqa', 'Qia', 'Uqi', 'Dqi',
            'Tqi', 'Qaqi', 'Qiqi', 'Sxqi', 'Spqi', 'Oqi', 'Nqi', 'Sxa', 'Usx',
            'Dsx', 'Tsx', 'Qasx', 'Qisx', 'Sxsx', 'Spsx', 'Osx', 'Nsx', 'Spa',
            'Usp', 'Dsp', 'Tsp', 'Qasp', 'Qisp', 'Sxsp', 'Spsp', 'Osp', 'Nsp',
            'Og', 'Uog', 'Dog', 'Tog', 'Qaog', 'Qiog', 'Sxog', 'Spog', 'Oog',
            'Nog', 'Na', 'Un', 'Dn', 'Tn', 'Qan', 'Qin', 'Sxn', 'Spn', 'On',
            'Nn', 'Ct', 'Uc'
        ];
        let logResult = Math.floor(Math.log10(input) / (3 * Math.log10(10)));
        if (input <= 999 && input > 0) {logResult = 0;}
        if (input <= 0) {return 0;}
        return ((Math.floor(input / Math.pow(1000, logResult), 3))).toString() + suffix[logResult];
    }

    function reapLemon() {
        props.setLemons(props.lemons+1);
    }

    function squeezeLemon() {
        if (props.lemons >= 2) {
            props.setLemons(props.lemons-2);
            props.setJuice(props.juice+1);
        }
    }

    function makeLemonade() {
        if (props.juice >= 4) {
            props.setJuice(props.juice-4);
            props.setLemonade(props.lemonade+1);
        }
    }

    function sellLemonade() {
        if (props.lemonade >= 1) {
            props.setLemonade(props.lemonade-1);
            props.setMoney(props.money+2);
        }
    }

return (
    <div id="middlebar">
        <h2>Resources:</h2>
        <div id="ressource" className="table">
            
            <table>
                <thead>
                    <tr>
                        <th className="legend"></th>
                        <th className="clickable lemons" onClick={reapLemon}>Lemons</th>
                        <th className="clickable juice" onClick={squeezeLemon}>Juice</th>
                        <th className="clickable lemonade" onClick={makeLemonade}>Lemonade</th>
                        <th className="clickable dollar" onClick={sellLemonade}>Dollar</th>
                        <th className="electricity">Electircity</th>
                        <th className="science">Science</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td className="legend">Amout:</td>
                        <td className="clickable lemons" onClick={reapLemon}>{format(props.lemons)}</td>
                        <td className="clickable juice" onClick={squeezeLemon}>{format(props.juice)}</td>
                        <td className="clickable lemonade" onClick={makeLemonade}>{format(props.lemonade)}</td>
                        <td className="clickable dollar" onClick={sellLemonade}>{format(props.money)}</td>
                        <td className="electricity">{format(props.electricity)}</td>
                        <td className="science">{format(props.science)}</td>
                    </tr>
                    <tr>
                        <td className="legend">Services per Click:</td>
                        <td className="clickable lemons" onClick={reapLemon}>One Free Lemon</td>
                        <td className="clickable juice" onClick={squeezeLemon}>Two Lemons for One Juice</td>
                        <td className="clickable lemonade" onClick={makeLemonade}>Four Juice for One Lemonade</td>
                        <td className="clickable dollar" onClick={sellLemonade}>One Lemonade for Two Dollar</td>
                        <td className="electricity">Impossible to make</td>
                        <td className="science">Impossible to make</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
  );
}

export default Resources;